<template>
  <div id="app" v-if="loggedIn">
    <div class="info__modal" :class="[ windowOrientation ? 'hide' : '']">
      <img src="@/assets/icons/logo_with_bg.svg">
      <p class="text-center">Für eine optimale Darstellung wechseln Sie bitte in den Landscapemodus</p>
    </div>
    <Header></Header>
    <div class="breadcrumb" v-if="showBreadcrumb">
        {{breadcrumbContent}}
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
import { Component, Vue } from "vue-property-decorator";
import Header from './components/partials/Header.vue';
import ltiService from './services/ltiService';
@Component({
  components: {
    Header,
  },
  data(){
    return{
      windowOrientation: null,
      windowWidth: 0,
      windowHeight: 0,
      loggedIn: false,
    }
  },
  created(){
    ltiService.checkLogin()
        .then(response => {
          this.loggedIn = true;
        })
        .catch(error => {
          console.log("There was an error:", error.response);
          console.log("REDIRECT");
          //window.location = 'https://www.meine-globus-lernwelt.de/login.php';
        });
  },
  mounted(){
    ltiService.getName()
        .then(response => {
          console.log(response)
          let name = response.data
          window.localStorage.setItem('name', name);
        })
        .catch(error => {
          console.log("There was an error:", error.response);
        });
    ltiService.getStatus()
        .then(response => {
          console.log(response)
          let status = response.data
          window.localStorage.setItem('status', status);
        })
        .catch(error => {
          console.log("There was an error:", error.response);
        });


  },
  computed:{
    showBreadcrumb(){
      if(this.$route.name == 'Intro01'){
        return false
      }
      return true
    },
    breadcrumbContent(){
      let out = '';
      switch(this.$route.name){
        case 'Intro02':
          out = 'Modul 1 / Vision und Marke / Begrüßung';
          break;
        case 'Overview':
          out = 'Modul 1 / Markthalle';
          break;
        case 'Vision':
          out = 'Modul 1 / Vision und Marke / Konzept Markthalle';
          break;
        case 'Mission':
          out = 'Modul 1 / Vision und Marke / Unsere Mission';
          break;
        case 'Schaetzfrage':
          out = 'Modul 1 / Vision und Marke / Schätzfrage';
          break;
        case 'Marke':
          out = 'Modul 1 / Vision und Marke / Markenpyramide';
          break;
        case 'Leistung':
          out = 'Modul 1 / Vision und Marke /  Leistungen';
          break;
        case 'Quiz':
          out = 'Modul 1 / Vision und Marke / Quiz';
          break;
      }
      return out;
    }
  }
})
export default class App extends Vue {}
</script>