import axios from "axios";


const apiClient = axios.create({
    baseUrl: "http://localhost:3000",
    withCredentials: false,
    headers: {
        Accept: "application/json",
        "Content-Type": "application/json"
    }
});

//let url = "https://www.meine-globus-lernwelt.de/Modules/VisionMarke/api.php";
let url = "http://localhost/testprovide/api.php";

export default {
    checkLogin(){
        return apiClient.get(url, {
            params: {
                action: 'checkLogin',
            }
        });
    },
    setStatus(s){
        return apiClient.get(url, {
            params: {
                action: 'setStatus',
                status: s
            }
        });
    },
    getStatus(){
        return apiClient.get(url, {
            params: {
                action: 'getStatus',
            }
        });
    },
    getName(){
        return apiClient.get(url, {
            params: {
                action: 'getName',
            }
        });
    }


};