<template>
  <div class="navigation">
    <router-link to="/overview" class="logo">
      <img src="@/assets/icons/Globus-Logo-1.svg">
    </router-link>
    <div class="menu" v-on:click="navOpen = !navOpen, getStatus()">
      <div class="menu__icon">
        <div></div>
        <div></div>
        <div></div>
      </div>
      <div class="menu__description">
        Menu
      </div>
    </div>
    <div class="nav__wrapper" :class="{ 'active' : navOpen}">
      <div class="nav__sickle">
        <div class="mobile__close" v-on:click="navOpen = false">
          <img src="@/assets/global/menu-close.svg">
        </div>
        <img src="@/assets/global/menu.svg" class="sickle">
        <div class="nav__wrapper--inner">


          <h2 class="text-white">MODUL 1</h2>
          <p class="font-18">Positionierung und Marke</p>
          <div class="nav-item flex mt-2 xl:mt-10">
            <div class="nav-item__status">
              <img src="@/assets/icons/chapter-done.svg" v-if="chapterStatus[0] == 1">
              <img src="@/assets/icons/chapter-open.svg" v-if="chapterStatus[0] == 2">
              <img src="@/assets/icons/chapter-disabled.svg" v-if="chapterStatus[0] == 3">
            </div>
            <router-link to="/chapter01" class="nav-item__info" :class="{'disabled' : chapterStatus[0] == 3}" v-on:click="navOpen = false">
              <div class="nav-item__headline">01 Vision</div>
            </router-link>
          </div>
          <div class="nav-item flex mt-2 xl:mt-10">
            <div class="nav-item__status">
              <img src="@/assets/icons/chapter-done.svg" v-if="chapterStatus[1] == 1">
              <img src="@/assets/icons/chapter-open.svg" v-if="chapterStatus[1] == 2">
              <img src="@/assets/icons/chapter-disabled.svg" v-if="chapterStatus[1] == 3">
            </div>
            <router-link to="/chapter02" class="nav-item__info" :class="{'disabled' : chapterStatus[1] == 3}" v-on:click="navOpen = false">
              <div class="nav-item__headline">02 Mission</div>
            </router-link>
          </div>
          <div class="nav-item flex mt-2 xl:mt-10">
            <div class="nav-item__status">
              <img src="@/assets/icons/chapter-done.svg" v-if="chapterStatus[2] == 1">
              <img src="@/assets/icons/chapter-open.svg" v-if="chapterStatus[2] == 2">
              <img src="@/assets/icons/chapter-disabled.svg" v-if="chapterStatus[2] == 3">
            </div>
            <router-link to="/chapter04" class="nav-item__info" :class="{'disabled' : chapterStatus[2] == 3}" v-on:click="navOpen = false">
              <div class="nav-item__headline">03 Markenpyramide</div>
            </router-link>
          </div>
          <div class="nav-item flex mt-2 xl:mt-10">
            <div class="nav-item__status">
              <img src="@/assets/icons/chapter-done.svg" v-if="chapterStatus[3] == 1">
              <img src="@/assets/icons/chapter-open.svg" v-if="chapterStatus[3] == 2">
              <img src="@/assets/icons/chapter-disabled.svg" v-if="chapterStatus[3] == 3">
            </div>
            <router-link to="/chapter05" class="nav-item__info" :class="{'disabled' : chapterStatus[3] == 3 }" v-on:click="navOpen = false">
              <div class="nav-item__headline">04 Leistungen</div>
            </router-link>
          </div>
          <div class="nav-item flex mt-2 xl:mt-10">
            <div class="nav-item__status">
              <img src="@/assets/icons/chapter-done.svg" v-if="chapterStatus[4] == 1">
              <img src="@/assets/icons/chapter-open.svg" v-if="chapterStatus[4] == 2">
              <img src="@/assets/icons/chapter-disabled.svg" v-if="chapterStatus[4] == 3">
            </div>
            <router-link to="/chapter06" class="nav-item__info" :class="{'disabled' : chapterStatus[4] == 3}" v-on:click="navOpen = false">
              <div class="nav-item__headline">Quiz</div>
            </router-link>
          </div>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
export default {
  name: "Header",
  data() {
    return {
      navOpen: false,
      status: 0,
    }
  },
  computed: {
    chapterStatus(){

      let s = parseFloat(this.status);

      let out  = [1,3,3,3,3];
      if(s == 0.2){
        out  = [1,2,3,3,3];
      } else if(s == 0.4){
        out  = [1,1,2,3,3];
      } else if(s == 0.6){
        out  = [1,1,1,2,3];
      } else if(s == 0.8){
        out  = [1,1,1,1,2];
      } else if(s == 1){
        out  = [1,1,1,1,1];
      }


      return out
    },
  },
  methods: {
    getStatus(){
      this.status = window.localStorage.getItem('status');
    }
  },
  watch:{
    $route (to, from){
      this.navOpen = false;
    },
    status (to, from){
      this.status = to;
    }
  }
}
</script>

<style scoped>

</style>